import { createSlice } from '@reduxjs/toolkit';
import { authApiEndpoints } from '../api/auth-api/endpoints';
import { userApiEndpoints } from '../api/user-api/endpoints';
// https://redux-toolkit.js.org/rtk-query/usage/examples
export const initialState = {
    data: null,
};
const slice = createSlice({
    name: 'user',
    initialState,
    reducers: {
        logOut: () => initialState,
    },
    extraReducers: (builder) => {
        builder
            .addMatcher(authApiEndpoints.endpoints.getUser.matchFulfilled, (state, action) => (Object.assign(Object.assign({}, state), { data: action.payload.user })))
            .addMatcher(authApiEndpoints.endpoints.getUser.matchRejected, (state, action) => {
            console.log('rejected', state, action);
        })
            .addMatcher(userApiEndpoints.endpoints.updateUser.matchFulfilled, (state, action) => (Object.assign(Object.assign({}, state), { data: action.payload.user })))
            .addMatcher(userApiEndpoints.endpoints.updateUser.matchRejected, (state, action) => {
            console.log('rejected', state, action);
        });
    },
});
export const { logOut } = slice.actions;
export default slice.reducer;
export const selectUser = (state) => state.user.data;
