import { createSlice } from '@reduxjs/toolkit';
import { ingredientApiEndpoints } from '../api';
const initialState = {
    data: [],
};
const slice = createSlice({
    name: 'ingredient',
    initialState,
    reducers: {
        setIngredient: (state, { payload: data }) => (Object.assign(Object.assign({}, state), { data })),
    },
    extraReducers: (builder) => {
        builder
            .addMatcher(ingredientApiEndpoints.endpoints.getIngredients.matchFulfilled, (state, action) => (Object.assign(Object.assign({}, state), { data: action.payload.data.map((x) => (Object.assign(Object.assign({}, x), { thumbnail: x.image, text: x.name }))) })))
            .addMatcher(ingredientApiEndpoints.endpoints.getIngredients.matchRejected, (state, action) => {
            console.log('rejected', state, action);
        });
    },
});
export const { setIngredient } = slice.actions;
export default slice.reducer;
export const selectIngredient = (state) => state.ingredient.data;
